import {SendSmsCode,GetPassByMobile} from 'api/user'
export default{
	name:'UserRegister',
	data(){
		return {
			/*密码*/
			password:'',
			/*再次输入*/
			rePassword:'',
			/*状态*/
			status:false,
			mobile:'',
			code:'',
		}
	},
	watch:{
		'rePassword'(value){
			if(value == this.password&&value){
				this.status = true
			}else{
				this.status = false
			}
		}
	},
	methods:{
		
		/*完成*/
		next(url){
			if(this.status){
				GetPassByMobile({
					passWord:this.password,
					repassword:this.rePassword,
					Code:this.code,
					UserName:this.mobile,
				}).then(res=>{
					if(res.result){
						uni.showToast({
							icon:'success',
							title: '重置成功'
						})
						uni.navigateTo({
						    url: url
						});
					}else{
						uni.showToast({
							title: res.msg
						})
					}
				})	
			}else{
				uni.showToast({
					icon:'none',
					title: '输入相同密码'
				})
			}
		}
	},
	onLoad (query) { 
	    this.mobile = query.mobile
		this.code = query.code
	}
}
